import React, { Component } from 'react';
import PropTypes from 'prop-types'
import './Menu.css';
import MenuButton from '../buttons/MenuButton';

class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = { active: false };
    }
    render () {
        const { value, onToggle } = this.props
        return (
            <div className = { "Menu " + this.props.menuState }>
                <div className="header">
                    <button className="closeButton"
                    onClick = { console.log("Closed menu") }>X</button>
                </div>                  
                <div className="content">
                    <MenuButton btnName="Home"></MenuButton>
                    <MenuButton btnName="Bio"></MenuButton>
                    <MenuButton btnName="Experience"></MenuButton>
                    <MenuButton btnName="Connect"></MenuButton>
                    <MenuButton btnName="About"></MenuButton>
                </div>
            </div>
        )
    }
}

Menu.propTypes = {
    value: PropTypes.number.isRequired
}

export default Menu;


